* {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100vh;
  background-color: #131417;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* This is for firefox. Horizontal scrollbars are transparent */
  scrollbar-color: transparent transparent;
  overflow-x: hidden;
  background-color: transparent;
  width: auto;
  height: auto;
  z-index: -100;
  /* background: url(https://im.ezgif.com/tmp/ezgif-1-7b923dec82.gif); */
  /* background: url(https://github.com/virejdasani/virejdasani.github.io/blob/master/dots-moving-video-bg-dark-optimized.gif?raw=true); */
  /* background: url(https://github.com/virejdasani/virejdasani.github.io/blob/master/dots-moving-video-bg-dark.gif?raw=true); */
  /* increase background size */
  background-size: 90%;
  transition: 1s opacity;
}

.video-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -100;
  /* let video fill screen */
  object-fit: cover;

}

html,
body {
  scroll-behavior: smooth !important;
}

html {
  min-height: 100%;
  background: #000;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

