#title {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  color: #FFF;
  text-align: center;
  font-family: "lato", sans-serif;
  font-weight: 300;
  font-size: 50px;
  letter-spacing: 10px;
  margin-top: -60px;
  padding-left: 10px;
}

#title span {
  background: -webkit-linear-gradient(white, #38495a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.pt-10 {
  padding-top: 12px;
  margin-bottom: -10px;
}

@keyframes animStar {
  from {
    transform: translateY(0px);
  }

  to {
    transform: translateY(-2000px);
  }
}
#titleHead {
  /* font-family: "Montserrat"; */
  font-family: 'JetBrains Mono', monospace;
  /* font-family: 'Shadows Into Light Two', cursive; */
  text-align: center;
  color: #FFF;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* height: 100vh; */
  margin-bottom: 0px;
  margin-left: 15px;
  margin-right: 15px;
  padding-top: 74px;
}

h1 {
  background-color: rgb(255, 255, 255);
  /* background-image: url(https://raw.githubusercontent.com/virejdasani/virejdasani.github.io/master/public/gif/loopedWaves.gif); */
  /* background-image: url(https://raw.githubusercontent.com/virejdasani/virejdasani.github.io/v2/public/gif/titleBG.gif); */
  color: transparent;
  background-size: cover;
  background-position: -10px;
  -moz-background-clip: text;
  -webkit-background-clip: text;
  text-transform: lowercase;
  margin: 10px 0;
  animation: spacify 2s ease;
  letter-spacing: 1px;
}

#tagline {
  font-family: 'Poppins', sans-serif;

  font-weight: lighter;
  color: rgb(171, 171, 171);
  animation: spacifyTagline 2s ease;
}

.taglineHighlights {
  /* background: linear-gradient(-45deg, #23d5ab, #23a6d5, #e73c7e, #ee7752);
  background-size: 500%;
  color: transparent;
  -moz-background-clip: text;
  -webkit-background-clip: text;
