  color: #FFF;

}

.buttonTopMargin {
  position: absolute;
  margin-bottom: 120px;
}

#titleButtons {
  margin: 10px 0;
}

.bn632-hover {
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  text-align: center;
  border: none;
  background-size: 300% 100%;
  border-radius: 50px;
  moz-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.bn632-hover:hover {
  background-position: 90% 10%;
  moz-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.bn632-hover:focus {
  outline: none;
  box-shadow: none;
}

.bn632-hover.bn25 {
  background-image: linear-gradient(to right,
      #29323c,
      #485563,
      #2b5876,
      #4e4376);
  box-shadow: 0 4px 15px 0 rgba(33, 39, 47, 0.75);
  background-color: transparent;
}

/* For width 200 - 399 */
@media only screen and (min-width: 200px) and (max-width: 399px) {
  h1 {
    font-size: 60px;
  }

  #tagline {
    font-size: 14px;
  }

  .bn632-hover {
    margin: 8px;
    width: 100px;
    font-size: 12px;
    height: 40px;
  }
}

/* For width 400 - 599 */
@media only screen and (min-width: 400px) and (max-width: 599px) {
  h1 {
    font-size: 84px;
  }

  #tagline {
    font-size: 16px;
  }

  .bn632-hover {
    margin: 10px;
    width: 120px;
    font-size: 14px;
    height: 45px;
  }
}

/* For width 600 - 999 */
@media only screen and (min-width: 600px) and (max-width: 999px) {
  h1 {
    font-size: 125px;
  }

  #tagline {
    font-size: 22px;
  }

  .bn632-hover {
    margin: 20px;
    width: 150px;
    font-size: 16px;
    height: 55px;
  }
}

/* For width 900+ */
@media only screen and (min-width: 1000px) {
  h1 {
    font-size: 135px;
  }

  #tagline {
    font-size: 24px;
  }

  .bn632-hover {
    margin: 20px;
    width: 150px;
    font-size: 16px;
    height: 55px;
  }
}


@keyframes spacify {
  from {
    letter-spacing: -10px;
    opacity: 0;
  }

  to {
    letter-spacing: 1px;
    opacity: 1;
  }
}

@keyframes spacifyTagline {
  from {
    letter-spacing: 2px;
    opacity: 0;
  }

  to {
    letter-spacing: 0px;
    opacity: 1;
  }
}
#navbar {
  background: rgba(45, 45, 45, 0.7);
  position: fixed;
  top: 0;
  width: 100%;
  padding-left: 10px;
  padding-top: 4px;
  padding-bottom: 4px;
  /* To make sure it is on top of any other content */
  z-index: 100;
}

#navbar a {
  float: left;
  display: block;
  padding-top: 7px;
  padding-bottom: 4px;
  border-radius: 4px;
}

#navbar a:hover {
  background-color: rgb(71, 71, 71);
}

/* For width < 399 */
@media only screen and (max-width: 399px) {
  #navbar a {
    padding-right: 7px;
    padding-left: 7px;
  }
}

/* For width 400+ */
@media only screen and (min-width: 400px) {
  #navbar a {
    padding-right: 10px;
    padding-left: 10px;
  }
}
